/* ==================== STYLES DE BASE ==================== */
.accueil-container {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  padding: 0 5%;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  color: white;
  box-sizing: border-box;

  overflow: hidden;

}

/* Effets de particules subtiles en arrière-plan */
.accueil-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(circle at 15% 50%, rgba(229, 62, 62, 0.1) 0%, transparent 20%),
    radial-gradient(circle at 85% 30%, rgba(229, 62, 62, 0.1) 0%, transparent 20%),
    radial-gradient(circle at 50% 80%, rgba(108, 99, 255, 0.1) 0%, transparent 20%);
  z-index: 0;
}

/* ----------------- SIDEBAR RÉSEAUX SOCIAUX ----------------- */
.social-sidebar {
  position: fixed;
  left: 40px;
  bottom: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 10;
}

.vertical-line {
  width: 1px;
  height: 200px;
  background: linear-gradient(to bottom, transparent, #fff, transparent);
  margin: 10px 0;
}

.social-icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.social-icons a {
  color: white;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
}

.social-icons a:hover {
  color: #6c63ff;
  transform: translateY(-3px);
  border-color: #6c63ff;
  box-shadow: 0 5px 15px rgba(108, 99, 255, 0.4);
}

/* ----------------- CONTENU PRINCIPAL ----------------- */
.content-wrapper {
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-left: 100px;
  padding: 100px 0;
  gap: 30px;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

/* Texte */
.text-section {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.text-section.fade-in {
  opacity: 1;
  transform: translateY(0);
}

/* Description ajoutée */
.accueil-description {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 1rem;
  color: #ccc;
  max-width: 500px;
}

/* Titre et sous-titre */
.accueil-title {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #ffffff 0%, #cccccc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.accueil-subtitle {
  font-size: 1.8rem;
  font-weight: 500;
  color: #e53e3e;
  overflow: hidden;
  white-space: nowrap;
  margin: 0;
  animation: typing 3.5s steps(30, end) infinite, blink 0.7s step-end infinite;
}

/* Ligne rouge */
.accueil-underline {
  width: 100px;
  height: 4px;
  background: linear-gradient(90deg, #e53e3e, #aa261c);
  margin: 10px 0 20px 0;
  animation: expand 1.5s ease-out forwards;
}

/* ----------------- IMAGE ----------------- */
.image-section {
  position: relative;
}

.image-section img {
  max-width: 100%;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.25);
  animation: breathing 4s ease-in-out infinite;
  position: relative;
  z-index: 2;
}

/* Éléments flottants technologies */
.floating-elements {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.floating-element {
  position: absolute;
  padding: 5px 12px;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 600;
  background: rgba(108, 99, 255, 0.15);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(108, 99, 255, 0.3);
  animation: float 6s ease-in-out infinite;
}

.floating-element.element-1 {
  top: 10%;
  left: -10%;
  animation-delay: 0s;
}

.floating-element.element-2 {
  top: 70%;
  right: -15%;
  animation-delay: 2s;
  background: rgba(229, 62, 62, 0.15);
  border: 1px solid rgba(229, 62, 62, 0.3);
}

.floating-element.element-3 {
  bottom: 10%;
  left: 20%;
  animation-delay: 4s;
  background: rgba(32, 201, 151, 0.15);
  border: 1px solid rgba(32, 201, 151, 0.3);
}

/* ----------------- BOUTON CONTACT ----------------- */
.contact-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  color: white;
  padding: 15px 30px;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 20px;
  position: relative;
  overflow: hidden;
  width: auto;
  max-width: 280px;
  background: linear-gradient(45deg, #ce0b0b, #aa261c);
  text-decoration: none;
  box-shadow: 0 5px 15px rgba(206, 11, 11, 0.3);
}

.contact-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(206, 11, 11, 0.4);
}

.contact-button:active {
  transform: translateY(0);
}

.contact-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: 0.5s;
}

.contact-button:hover::before {
  left: 100%;
}

/* Indicateur de défilement */
.scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #999;
  font-size: 0.8rem;
  z-index: 2;
}

.scroll-line {
  width: 1px;
  height: 50px;
  background: linear-gradient(to bottom, #e53e3e, transparent);
  margin-top: 10px;
  animation: scroll-line 2s infinite;
}

/* ==================== ANIMATIONS ==================== */

@keyframes typing {
  0% { width: 0; }
  40% { width: 100%; }
  60% { width: 100%; }
  100% { width: 100%; }
}

@keyframes blink {
  50% { border-color: transparent; }
}

@keyframes expand {
  from { width: 0; }
  to { width: 100px; }
}

@keyframes breathing {
  0% { transform: scale(0.98); }
  50% { transform: scale(1.02); }
  100% { transform: scale(0.98); }
}

@keyframes fadeIn {
  to { opacity: 1; transform: translateY(0); }
}

@keyframes float {
  0% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-15px) rotate(5deg); }
  100% { transform: translateY(0) rotate(0deg); }
}

@keyframes scroll-line {
  0% { height: 0; opacity: 0; }
  50% { height: 50px; opacity: 1; }
  100% { height: 0; opacity: 0; }
}

.animate-title {
  opacity: 0;
  animation: fadeIn 1s ease-out 0.5s forwards;
}

/* ==================== RESPONSIVE ==================== */

/* Tablet & PC medium */
@media (max-width: 1024px) {
  .social-sidebar {
    display: none;
  }
  
  .content-wrapper {
    margin-left: 0;
    flex-direction: column;
    text-align: center;
    gap: 40px;
    padding: 80px 0;
  }
  
  .text-section { 
    align-items: center; 
    max-width: 100%;
  }
  
  .accueil-description {
    text-align: center;
  }
  
  .image-section img { 
    max-width: 70%; 
  }
  
  .floating-elements {
    display: none;
  }
}

/* Small Tablet */
@media (max-width: 768px) {
  .accueil-title { 
    font-size: 2.8rem; 
    text-align: center;
  }
  
  .accueil-subtitle { 
    font-size: 1.5rem; 
  }
  
  .accueil-description {
    font-size: 1rem;
    padding: 0 10px;
  }
  
  .image-section img { 
    max-width: 80%; 
  }
  
  .contact-button { 
    padding: 12px 25px; 
    font-size: 1rem; 
  }
}

/* Mobile */
@media (max-width: 480px) {
  .accueil-container {
    padding: 0 20px;
  }
  
  .accueil-title { 
    font-size: 2.2rem; 
    line-height: 1.2;
  }
  
  .accueil-subtitle { 
    font-size: 1.2rem; 
  }
  
  .image-section img { 
    max-width: 90%; 
  }
  
  .contact-button { 
    width: 100%; 
    max-width: 280px;
    padding: 12px 20px; 
    font-size: 0.95rem; 
  }
  
  .content-wrapper {
    padding: 60px 0;
  }
  
  .scroll-indicator {
    bottom: 20px;
  }
}

/* Desktop uniquement */
@media (min-width: 1025px) {
  .content-wrapper {
    margin-left: 100px;
  }
  .accueil-container {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  padding: 0 5%;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  color: white;
  box-sizing: border-box;
  overflow: hidden;
  top: -70px;
}

  .breathing-image{
     width: 730px;
  }
}